@import "../variables";

.mdc-select__bottom-line {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  transform: scaleY(1);
  transform-origin: bottom;
  transition: $mdc-select-menu-transition;

  &::after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    transform: scaleX(0);
    transition: $mdc-select-menu-transition;
    opacity: 0;
    content: "";
    z-index: 2;
  }
}

.mdc-select__bottom-line--active::after {
  opacity: 1;
}



